            <div class="item">租户号: {{showCheck.vlanId}}</div>
          <div class="item">名称: {{showCheck.routeName}}</div>
          <div class="item">网关组ID: {{showCheck.gatewayGroupId}}</div>
          <div class="item">主叫前缀: {{showCheck.callerPrefix}}</div>
          <div class="item">被叫前缀: {{showCheck.calleePrefix}}</div>
