</template>

<script>

  import {
    voipRoutersList,
    savevoipRouters,
    deletevoipRoutersByIds,
    selectAllGatewayGroup,
    getAllChangeID,
    getAllNotworktimeID,
    getDirectionData,

  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';

  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        voipRoutersData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗
        directionDataDisplay:'',//方向数据输入框是否隐藏
        gatewayGroupIdDisplay:'',//网关组ID是否隐藏
        localDisplay:'',//代表是否隐藏两个变换规则和工作时间id，留言内线号码
        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        voipRoutersForm: {
          vlanId: '',
          routeName: '',
          gatewayGroupId: '',
          callDirection: '',
          directionData: '',
          callerPrefix: '',
          calleePrefix: '',
          callerSuffix: '',
          calleeSuffix: '',
          callerNumChangeId: '',
          calleeNumChangeId: '',
          // notworktimeId: '',
          vmNumber: '',
          description: '',
        },
        voipRoutersEditForm: {
             vlanId: '',
             routeName: '',
             gatewayGroupId: '',
             callDirection: '',
             directionData: '',
             callerPrefix: '',
             calleePrefix: '',
             callerSuffix: '',
             calleeSuffix: '',
             callerNumChangeId: '',
             calleeNumChangeId: '',
            //  notworktimeId: '',
             vmNumber: '',
             description: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,validator:checkNumber, message:'请输入租户号(数字)',trigger: 'blur'}],
           gatewayGroupId: [{validator: checkNull, trigger: 'blur'}],
           callDirection: [{required: true,  validator: checkNumber, trigger: 'blur'}],
           callerNumChangeId: [{validator: checkNull, trigger: 'blur'}],
           calleeNumChangeId: [{validator: checkNull, trigger: 'blur'}],
          //  notworktimeId: [{validator: checkNull, trigger: 'blur'}],
         
        },
        directionDataInfo:[],//方向数据，即当前租户IVR主菜单
        callDirection:[{
            value:  0,
            label: '本地'
        },{
            value: 1,
            label: '网关'
        },{
            value: 2,
            label: 'IVR'
        },{
            value: 3,
            label: '语音留言'
        }],
        gatewayGroup:[{
          value:-1,
          label:' ',
        }],
        allChangeID:[{
          value: -1,
          label: ' '
        }],
        allChangeIDobj:[{}],
        allNotworktime:[{
          value:-1,
          label:' ',
        }],
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
        
        }
        
    },
    created() {
      this.getvoipRoutersList(); //加载数据
      this.selectAllGatewayGroup();
      this.getAllChangeID();
      // this.getAllNotworktimeID();
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getvoipRoutersList() {
        try {
          let query = {
            routeName: this.searchInput
          }
          //分页
          const res = await voipRoutersList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.voipRoutersData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var dir = item.callDirection;
              if(dir == 0){
                dir = '本地';
              }else if(dir == 1){
                dir = '网关';
              }else if(dir == 2){
                dir = 'IVR';
              }else{
                dir = '语音留言';
              };
              this.$data.allChangeIDobj[-1] = -1;
              if(item.gatewayGroupId == -1){
                item.gatewayGroupId = '';
              }
              if(item.callerNumChangeId == -1){
                item.callerNumChangeId = '';
              }
              if(item.calleeNumChangeId == -1){
                item.calleeNumChangeId = '';
              }
              // if(item.notworktimeId == -1){
              //   item.notworktimeId = '';
              // }
              const tableItem = {
                callDirection:dir,
                calleeNumChangeId:this.$data.allChangeIDobj[item.calleeNumChangeId] = item.calleeNumChangeId,
                calleePrefix:item.calleePrefix,
                calleeSuffix:item.calleeSuffix,
                callerNumChangeId:this.$data.allChangeIDobj[item.callerNumChangeId] = item.callerNumChangeId,
                callerPrefix:item.callerPrefix,
                callerSuffix:item.callerSuffix,
                createAt:item.createAt,
                description:item.description,
                directionData:item.directionData,
                gatewayGroupId:item.gatewayGroupId,
                // notworktimeId:item.notworktimeId,
                routeId:item.routeId,
                routeName:item.routeName,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vmNumber:item.vmNumber,
                vlanId:item.vlanId,
              }
              this.voipRoutersData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         if(args.gatewayGroupId == undefined || args.gatewayGroupId === '')delete args.gatewayGroupId;
         if(args.callDirection == undefined || args.callDirection === '')delete args.callDirection;
         if(args.callerNumChangeId == undefined || args.callerNumChangeId === '')delete args.callerNumChangeId;
         if(args.calleeNumChangeId == undefined || args.calleeNumChangeId === '')delete args.calleeNumChangeId;
        //  if(args.notworktimeId == undefined || args.notworktimeId === '')delete args.notworktimeId;
         if(args.callerPrefix == undefined || args.callerPrefix === '')args.callerPrefix = '.*';
         if(args.calleePrefix == undefined || args.calleePrefix === '')args.calleePrefix = '.*';
         if(args.callerSuffix == undefined || args.callerSuffix === '')args.callerSuffix = '.*';
         if(args.calleeSuffix == undefined || args.calleeSuffix === '')args.calleeSuffix = '.*';
         return args;
      },
      vlanIdChange(formName){
        if(formName == 'voipRoutersForm'){
          this.getDirectionData(this.$data.voipRoutersForm.vlanId);
        } else {
          this.getDirectionData(this.$data.voipRoutersEditForm.vlanId);
        }
      },
      async getDirectionData(vlanId){
        try {
          const res = await getDirectionData({vlanId:vlanId});
          if(res.resultCode == '0'){
            var tempDD = [];
            for (var value of res.result){
                var temp = {};
                temp.label = value.ivrMenuName;
                temp.value = value.ivrMenuName;
                tempDD.push(temp);
            }
            this.$data.directionDataInfo = tempDD;
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      //根据添加页面呼叫顺序，判断是否隐藏网关组ID和方向数据
      setDisplay(){
        if(this.$data.voipRoutersForm.callDirection == 0){
          this.localDisplay = 'none';
          this.directionDataDisplay = 'none';
          this.gatewayGroupIdDisplay = 'none';
        } else if(this.$data.voipRoutersForm.callDirection == 3){
          this.localDisplay = '';
          this.directionDataDisplay = 'none';
          this.gatewayGroupIdDisplay = 'none';
        } else if(this.$data.voipRoutersForm.callDirection == 1){
          this.localDisplay = '';
          this.directionDataDisplay = 'none';
          this.gatewayGroupIdDisplay = '';
        } else {
          this.localDisplay = '';
          this.directionDataDisplay = '';
          this.gatewayGroupIdDisplay = 'none';
        }
        this.$data.voipRoutersForm.directionData = '';
        this.$data.voipRoutersForm.gatewayGroupId = -1;
      },
      //根据编辑页面呼叫顺序，判断是否隐藏网关组ID和方向数据
      setEditFormDisplay(){
        if(this.$data.voipRoutersEditForm.callDirection == 0){
          this.localDisplay = 'none';
          this.directionDataDisplay = 'none';
          this.gatewayGroupIdDisplay = 'none';
        } else if(this.$data.voipRoutersEditForm.callDirection == 3){
          this.localDisplay = '';
          this.directionDataDisplay = 'none';
          this.gatewayGroupIdDisplay = 'none';
        } else if( this.$data.voipRoutersEditForm.callDirection == 1){
          this.localDisplay = '';
          this.directionDataDisplay = 'none';
          this.gatewayGroupIdDisplay = '';
        } else {
          this.localDisplay = '';
          this.directionDataDisplay = '';
          this.gatewayGroupIdDisplay = 'none';
        }
        this.$data.voipRoutersEditForm.directionData = '';
        this.$data.voipRoutersEditForm.gatewayGroupId = -1;
      },

      async selectAllGatewayGroup() {
        try {
            const res = await selectAllGatewayGroup();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.label = value.groupId;
                   temp.value = value.groupId;
                   this.$data.gatewayGroup.push(temp);
                   
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      //获取所有号码变换规则ID
      async getAllChangeID() {
        try {
            const res = await getAllChangeID();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.label = value.description;
                   temp.value = value.changeId;
                   this.$data.allChangeID.push(temp);
                   this.$data.allChangeIDobj[value.changeId]=value.description;
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      //获取所有的非工作日
      async getAllNotworktimeID () {
        try {
            const res = await getAllNotworktimeID();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.label = value.notworktimeId;
                   temp.value = value.notworktimeId;
                   this.$data.allNotworktime.push(temp);
                   
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },


      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getvoipRoutersList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleVoipRoutersSearch() {
        this.getvoipRoutersList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //增加
      handleVoipRoutersAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        this.voipRoutersForm = {
          routeName: '',
          gatewayGroupId: -1,
          callDirection: '',
          directionData: '',
          callerPrefix: '',
          calleePrefix: '',
          callerSuffix: '',
          calleeSuffix: '',
          callerNumChangeId: -1,
          calleeNumChangeId: -1,
          // notworktimeId: -1,
          vmNumber: '',
          description: '',
          vlanId: tempVlanId,
        };
        this.getDirectionData(this.$data.voipRoutersForm.vlanId);
        this.dialogFormVisible = true;
        
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deletevoipRoutersByIds(data);
              if (result.resultCode == '0') {
                that.getvoipRoutersList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleVoipRoutersDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deletevoipRoutersByIds(data);
            if (result.resultCode == '0') {
              that.getvoipRoutersList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleVoipRoutersEdit(index, row) {
        if (typeof row !== 'undefined') {
            var dir = row.callDirection;
              if(dir == '本地'){
                dir = 0;
                this.directionDataDisplay = 'none';
                this.gatewayGroupIdDisplay = 'none';
                this.localDisplay = 'none';
              }else if(dir == '网关'){
                dir = 1;
                this.directionDataDisplay = 'none';
                this.gatewayGroupIdDisplay = '';
                this.localDisplay = '';
              }else if(dir == 'IVR'){
                dir = 2;
                this.directionDataDisplay = '';
                this.gatewayGroupIdDisplay = 'none';
                this.localDisplay = '';
              }else{
                this.directionDataDisplay = 'none';
                this.gatewayGroupIdDisplay = 'none';
                this.localDisplay = '';
                dir = 3;
              }
            this.voipRoutersEditForm.vlanId = row.vlanId;
            this.voipRoutersEditForm.routeName = row.routeName;
            this.voipRoutersEditForm.gatewayGroupId = row.gatewayGroupId;
            this.voipRoutersEditForm.callDirection = dir;
            this.voipRoutersEditForm.directionData = row.directionData;
            this.voipRoutersEditForm.callerPrefix = row.callerPrefix;
            this.voipRoutersEditForm.calleePrefix = row.calleePrefix;
            this.voipRoutersEditForm.callerSuffix = row.callerSuffix;
            this.voipRoutersEditForm.calleeSuffix = row.calleeSuffix;
            this.voipRoutersEditForm.callerNumChangeId = row.callerNumChangeId;
            this.voipRoutersEditForm.calleeNumChangeId = row.calleeNumChangeId;
            // this.voipRoutersEditForm.notworktimeId = row.notworktimeId;
            this.voipRoutersEditForm.vmNumber = row.vmNumber;
            this.voipRoutersEditForm.description = row.description;
            this.voipRoutersEditForm.uuid = row.uuid;
            this.getDirectionData(row.vlanId);
        }
        this.dialogFormEdit = true;
      },
      //查看
      handlevoipRoutersCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleVoipRoutersSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savevoipRouters(params);
              if (result.resultCode == '0') {
                  if(formName == 'voipRoutersForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getvoipRoutersList();
                  this.$message({
                     type: 'success',
                     message: '路由管理保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
