<template>
    <div class="globlgl">
        <div class="btns">
            <el-button class="float-l btn" @click="handleVoipGatewayAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
             <el-input class="search-input"  :maxlength="128" placeholder="网关名称" icon="search" v-model="searchInput" :on-icon-click="handleVoipGatewaySearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="voipGatewayData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handlevoipGatewayCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="gatewayName" label="网关名称"></el-table-column>
            <el-table-column prop="remoteAddr" label="网关地址"></el-table-column>
            <el-table-column prop="remotePort" label="网关端口"></el-table-column>
            <el-table-column prop="priority" label="优先级"></el-table-column>
            <!-- <el-table-column prop="limitType" label="限定类型"></el-table-column> -->
            <el-table-column prop="status" label="网关状态"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleVoipGatewayDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleVoipGatewayEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加网关管理-->
        <el-dialog title="添加网关管理" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="voipGatewayForm" :rules="formRules" labelPosition="left" ref="voipGatewayForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="voipGatewayForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="gatewayName" label="网关名称" :label-width="formLabelWidth">
                    <el-input v-model="voipGatewayForm.gatewayName"  :maxlength="128" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="remoteAddr" label="网关地址" :label-width="formLabelWidth">
                    <el-input v-model="voipGatewayForm.remoteAddr"  :maxlength="256" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="remotePort" label="网关端口" :label-width="formLabelWidth">
                    <el-input v-model.number="voipGatewayForm.remotePort" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="priority" label="优先级" :label-width="formLabelWidth">
                    <el-input v-model.number="voipGatewayForm.priority" auto-complete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item prop="limitType" label="限定类型" :label-width="formLabelWidth">
                    <el-select v-model.number="voipGatewayForm.limitType" placeholder="请选择">
                        <el-option  v-for="item in limitTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item> -->
                <el-form-item prop="status" label="网关状态" :label-width="formLabelWidth">
                    <el-select v-model.number="voipGatewayForm.status" placeholder="请选择">
                        <el-option  v-for="item in statusTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleVoipGatewaySave('voipGatewayForm')" :disabled="isOpen" >确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑网关管理-->
        <el-dialog title="编辑网关管理" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="voipGatewayEditForm" :rules="formRules" labelPosition="left" ref="voipGatewayEditForm">
                <el-form-item prop="vlanId" label="租户名称" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="voipGatewayEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="gatewayName" label="网关名称" :label-width="formLabelWidth">
                    <el-input v-model="voipGatewayEditForm.gatewayName" :maxlength="128" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="remoteAddr" label="网关地址" :label-width="formLabelWidth">
                    <el-input v-model="voipGatewayEditForm.remoteAddr" :maxlength="256" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="remotePort" label="网关端口" :label-width="formLabelWidth">
                    <el-input v-model.number="voipGatewayEditForm.remotePort" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="priority" label="优先级" :label-width="formLabelWidth">
                    <el-input v-model.number="voipGatewayEditForm.priority" auto-complete="off"></el-input>
                    
                </el-form-item>
                <!-- <el-form-item prop="limitType" label="限定类型" :label-width="formLabelWidth">
                    <el-select v-model.number="voipGatewayEditForm.limitType" placeholder="请选择">
                        <el-option  v-for="item in limitTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item> -->
               <el-form-item prop="status" label="网关状态" :label-width="formLabelWidth">
                    <el-select v-model.number="voipGatewayEditForm.status" placeholder="请选择">
                        <el-option  v-for="item in statusTypeArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleVoipGatewaySave('voipGatewayEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看网关管理" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">网关名称: {{showCheck.gatewayName}}</div>
            <!-- <div class="item">限定类型: {{showCheck.limitType}}</div> -->
            <div class="item">优先级: {{showCheck.priority}}</div>
            <div class="item">网关地址: {{showCheck.remoteAddr}}</div>
            <div class="item">网关端口: {{showCheck.remotePort}}</div>
            <div class="item">网关状态: {{showCheck.status}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    voipGatewayList,
    savevoipGateway,
    deletevoipGatewayByIds
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';

  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else if(value<0 || value>100){
                callback(new Error('请输入0-100以内的数字'));
            }else{
                callback();
            }
        };
        var checkNum = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        voipGatewayData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗
        isOpen: false,
        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        voipGatewayForm: {
          vlanId: 0,
          gatewayName: '',
          remoteAddr: '',
          remotePort: '',
          priority: 100,
          limitType: 0,
          status: '',
        },
        voipGatewayEditForm: {
          vlanId: 0,
          gatewayName: '',
          remoteAddr: '',
          remotePort: '',
          priority: 100,
          limitType: 0,
          status: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,validator:checkNum, message:'请输入租户号(数字)',trigger: 'blur'}],
           remotePort: [{validator: checkNull, trigger: 'blur'}],
           priority: [{required: true,validator: checkNumber, trigger: 'blur'}],
           limitType: [{validator: checkNull, trigger: 'blur'}],
           status: [{validator: checkNull, trigger: 'blur'}],
         
        },
        limitTypeArr:[{
          value: 0,
          label: '不做任何限定'
          },{
          value: 1,
          label: '被叫黑名单启用'
        }],
        statusTypeArr:[{
          value: 0,
          label: '不可用'
        },{
          value: 1,
          label: '可用'
        }],
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getvoipGatewayList(); //加载数据
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getvoipGatewayList() {
        try {
          let query = {
            gatewayName: this.searchInput
          }
          //分页
          const res = await voipGatewayList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.voipGatewayData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var limitType = '不做任何限定';
              if(item.limitType==1){
                  limitType = '被叫黑名单启用';
              }
              var status = '不可用';
              if(item.status==1){
                status = '可用';
              }
              const tableItem = {
                createAt:item.createAt,
                gatewayId:item.gatewayId,
                gatewayName:item.gatewayName,
                limitType:limitType,
                priority:item.priority,
                remoteAddr:item.remoteAddr,
                remotePort:item.remotePort,
                status:status,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.voipGatewayData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
         if(args.remotePort == undefined || args.remotePort === '')delete args.remotePort;
         if(args.priority == undefined || args.priority === '')delete args.priority;
         if(args.limitType == undefined || args.limitType === '')delete args.limitType;
         if(args.status == undefined || args.status === '')delete args.status;
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getvoipGatewayList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleVoipGatewaySearch() {
        this.getvoipGatewayList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      //增加
      handleVoipGatewayAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        this.voipGatewayForm = {
          gatewayName: '',
          remoteAddr: '',
          remotePort: '',
          priority: 100,
          limitType: 0,
          vlanId: tempVlanId,
          status: 1,
        };
        this.dialogFormVisible = true;
        this.isOpen = false;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deletevoipGatewayByIds(data);
              if (result.resultCode == '0') {
                that.getvoipGatewayList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleVoipGatewayDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deletevoipGatewayByIds(data);
            if (result.resultCode == '0') {
              that.getvoipGatewayList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleVoipGatewayEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.voipGatewayEditForm.vlanId = row.vlanId;
            this.voipGatewayEditForm.gatewayName = row.gatewayName;
            this.voipGatewayEditForm.remoteAddr = row.remoteAddr;
            this.voipGatewayEditForm.remotePort = row.remotePort;
            this.voipGatewayEditForm.priority = row.priority;
            //this.voipGatewayEditForm.limitType = row.limitType;
            if(row.status=='可用'){
              this.voipGatewayEditForm.status = 1;
            } else {
              this.voipGatewayEditForm.status = 0;
            }
            this.voipGatewayEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handlevoipGatewayCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleVoipGatewaySave(formName) {
        this.isOpen = true;
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savevoipGateway(params);
              if (result.resultCode == '0') {
                  if(formName == 'voipGatewayForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getvoipGatewayList();
                  this.$message({
                     type: 'success',
                     message: '网关管理保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>


